* {
  margin: 0;
  padding: 0;
  /* border: 1px solid #000; */
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1 {
  text-align: center;
  padding: 25px;
}
.content {
  width: 1200px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}
.match-title > span {
  width: 150px;
  text-align: center;
  font-weight: bold;
}
.match-title {
  display: flex;
  justify-content: space-between;
}
.match-content {
  display: flex;
  justify-content: space-between;
}
.r16, .qf, .sf, .final {
  width: 150px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  justify-content: space-around;
}
.match {
  height: 100px;
  border: 1px solid #c0c0c0;
  border-radius: 5px;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
}
.match-up, .match-down {
  display: flex;
  column-gap: 5px;
}
.right .match .match-up, 
.right .match .match-down {
  flex-direction: row-reverse;
  text-align: right;
}
.lose-match {
  opacity: 0.8;
}
.win-match {
  font-weight: bold;
}
hr {
  height: 1px;
  margin: 5px 0;
  color: inherit;
  background-color: currentColor;
  border: 0;
  opacity: .25;
}
.match .flag {
  font-size: 22pt;
}
.match .name {
  font-size: 11pt;
  display: flex;
  flex-direction: column;
  justify-content: center;
  white-space: nowrap;
}
.winner {
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 100px;
  text-align: center;
  position: relative;
}
.winner-label {
  position: absolute;
  font-size: 22px;
  bottom: -20px;
  left: 50%;
  transform: translate(-50%, -50%);
}
.social {
  margin: 0 auto;
  width: 1200px;
  padding: 20px;
}
.btn-download, .btn-fb-share, .btn-save {
  margin-top: 10px;
  padding: 10px 20px;
  cursor: pointer;
}

/* The Modal (background) */
.modal {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content */
.modal-content {
  background-color: #fefefe;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #888;
  max-width: 300px;
  position: relative;
}

/* The Close Button */
.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  position: absolute;
  top: 15px;
  right: 20px;
  cursor: pointer;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.slider {
  text-align: center;
  margin: 25px 0;
}
.slider input {
  width: 100%;
}

.win-label {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.control {
  display: flex;
  justify-content: flex-end;
}